body {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  background-color: rgba(63, 248, 103, 0.71);
}

.container {
  width: 50%;
  margin: 80px auto;
}

.title {
  text-align: center;
  font-size: 48px;
}

.container-area {
  width: 60%;
  margin: 40px auto;
}

.input {
  border: none;
  outline: none;
  box-sizing: border-box;
}

.input-area {
  background-color: #ffffff;
  font-size: 16px;
  width: 100%;
  height: 50px;
  box-sizing: border-box;
  border-radius: 8px;
  border: none;
  padding: 10px;
}

.list-row {
  padding: 0;
  position: relative;
}

.list {
  list-style: none;
  box-sizing: border-box;
  line-height: 1.5;
  font-weight: 700;
  background-color: #f0f0f0;
  font-size: 16px;
  width: 100%;
  border-radius: 8px;
  border: none;
  padding: 25px 8px;
  margin-bottom: 20px;
}


.item {
  cursor: pointer;
  margin-left: 10px;
  margin-top: 8px;
}

.edit-input {
  border: none;
  background-color: #f0f0f0;
  font-size: 16px;
}

.fa-pen-square {
  position: absolute;
  right: 60px;
  cursor: pointer;
  margin-top: 5px;
}

.fa-trash {
  position: absolute;
  right: 10px;
  width: 30px;
  cursor: pointer;
  margin-top: 5px;
}